'use strict';
// ------------------------     -------------------------------

export const positionBtnsContainer = () => {
  const movieModalContainer = document.querySelector('.modal-movie'); // 
  const div = document.querySelector('.modal-movie__container'); //     
  const btnsWrap = document.querySelector('.modal-movie__command-btns-wrap'); //  

  //   padding-top 
  const modalStyles = window.getComputedStyle(movieModalContainer);
  const ModalPaddingTop = Number.parseFloat(modalStyles.paddingTop); //  padding-top   'px'
  const ModalPaddingRight = Number.parseFloat(modalStyles.paddingRight); //  padding-right   'px'

  //  width    (<div>)
  const btnsWrapStyles = window.getComputedStyle(btnsWrap);
  const dinamicWidthOfBtnsWrap = Number.parseFloat(btnsWrapStyles.width);

  //   height  width    <div>
  const divStyles = window.getComputedStyle(div);
  const dinamicTopCoordinateForBtns =
    Number.parseFloat(divStyles.height) + ModalPaddingTop + 20 + 'px'; //  
  const dinamicRightCoordinateForBtns =
    Number.parseFloat(divStyles.width) +
    ModalPaddingRight -
    dinamicWidthOfBtnsWrap +
    'px'; //  

  btnsWrap.style.top = dinamicTopCoordinateForBtns; //    
  btnsWrap.style.right = dinamicRightCoordinateForBtns; //    
};
